	</div>
</template>

<script>
	export default{
		name:"wait",
		props:['show'],
		data(){
			return {
				
			}
		}
		
		
	}
</script>

<style>
	.wait{ position: fixed; top:0px; left: 0px; width:100%; height: 100%; z-index: 30;}
	.waitk {width:100%; height: 100%; position: relative;}
	.waitk img{ position: absolute; left: 0px; right: 0px; top:50%; width:60px; margin: auto;
	transform:translateY(-70%);-webkit-transform:translateY(-70%);
	}
</style>


