<template>
	<div class="hotcity">
        <search @searchContent="search" :city="cityName" ></search>
        <div class="hotcity_list">
        	
        	 <none :show="noneShow" style=" height: 100%;"></none>
        	<ul v-for="item in splist">
        		<li>
        			<a :href="item | getUrl">
	        			<div class="hotcity_list_img">
	        				<img :src="item.picture"/>
	        				<div class="hotcity_list_total">￥&nbsp;{{item.price}}&nbsp;/&nbsp;晚</div>
	        			</div>
	        			<div class="hotcity_list_content">
	        				<div  class="hotcity_list_content_left">
	        					<div class="hotcity_list_content_title">{{item.title}}</div>
	        				    <div class="hotcity_list_content_xq">
	        				    	整套-{{item.room}}室{{item.office}}厅{{item.guard}}卫 | 可住{{item.canlive}}人 
	        				    </div>
	        				</div>
	        				
	        				<img :src="item.headimgurl" class="hotcity_list_content_tx"/>
	        			</div>
	        		</a>
        		</li>
        	</ul>
        </div>
       
	</div>
</template>
<style type="text/css">
	.hotcity{ padding-bottom: 7rem;}
	.hotcity ul{ margin: 0; margin-top:0.7rem; }
	.hotcity ul li{ list-style-type: none;  background: #fff; margin-bottom: 0.7rem;}
	.hotcity ul li .hotcity_list_img{ position: relative;}
	.hotcity ul li .hotcity_list_img img{ width:100%; height: auto;}
	.hotcity ul li .hotcity_list_img .hotcity_list_total{ z-index: 2; position: absolute; left: 0px; bottom:1rem;
	   padding: 1rem 2rem; background: rgba(255,65,79,0.7); border-radius:0rem 2rem 2rem 0rem; height:1rem ;line-height: 1rem;
	   text-align: center; color: #fff; font-size: 1.4rem;
	}
	.hotcity_list ul li{ padding: 1rem 1rem;}
	.hotcity_list_content{  display: flex; display: -webkit-flex; flex-flow:row nowrap;}
	.hotcity_list_content_left{ flex:auto}
	.hotcity_list_content_title{ font-size: 1.4rem; padding: 0.5rem 0rem;}
	.hotcity_list_content_xq{ font-size: 1rem; padding: 0.5rem 0rem;}
	.hotcity_list_content img{ position: static; z-index: 3; flex:none; width:5rem ; height: 5rem; border-radius:50%; margin-top: -2.5rem; margin-right: 1rem;}
	
</style>
<script>
import search from "@/components/search"
import none from '@/components/none.vue'
export default{
	name:"hotcity",
	components:{
		search,
		none,
	},
	filters:
	{
	    	getUrl(o)
	    	{
	    		return "/index.php/weixin/index/detail?id="+o.id;
	    	}
	},
	data(){
		return {
			cityId:"",
			cityName:"",
			searchContnt:{},
			splist:"",
			noneShow:false,
		}
	},
	methods:{
		search(res){
			var that=this;
		   that.searchContent=res;
		   that.cityName=res.city;
		   if(that.searchContent.cid==0)
		   {
		   	 that.searchContent.cid=this.$route.params.id;
		   }
		   
		   this.noneShow=false;
		   this.$ajax.post(
	   	   	'http://minsu.youfai.cn/index.php/weixin/index/getminsulist',
	   	   	that.searchContent).then(function(res){
	   	   		that.splist=res.data.list;
	   	   		if(res.data.status==2){
	   	   			that.noneShow=true;
	   	   		}
	   	   		
	   	   	}).catch(function(er){alert(er);that.noneShow=false;})
		},
		pageInit()
		{
	   	   var that=this;
	   	   var p={
					city:this.city,
					startD:"入住日期",
					endD:"退房日期",
					cid:that.cityId,
					//night:null,
					//ren:null,
				}
	   	   
	   	   this.$ajax.post(
	   	   	'http://minsu.youfai.cn/index.php/weixin/index/getminsulist',
	   	   	 p).then(function(res){
	   	   		that.splist=res.data.list;
	   	   		if(res.data.status==2){
	   	   			setTimeout(function(){that.noneShow=true;},500)
	   	   			
	   	   		}
	   	   		
	   	   	}).catch(function(er){alert(er);that.noneShow=false;})
	   },
	   getOps(){
	   	     this.cityId=this.$route.params.id;
			 this.cityName=this.$route.params.title;
			 this.pageInit();
	   },
	},
	mounted(){
		        this.getOps()
	},
	activated(){
		this.noneShow=false;
	},
	watch:{
		'$route' (to, from) 
		   {
			   	if(from.name=="Index")
			   	{
		
			   		this.getOps();
			   	}
		   }
	}
	
	
}
</script>




