<style>
	.myconfirm{
		position:fixed; width:100%; height: 100%; background: rgba(0,0,0,0.5); z-index: 15; top:0px; left: 0px;
	}
	.myconfirm .myconfirmk{ width:100%; height: 100%; background: none;}
	.myconfirm .myconfirmk .myconfirm_main{ max-width: 400px; border-radius:5px; overflow: hidden;; width:80%; height: auto; background: #fff; margin: 0px auto; margin-top: 50%;
	  transform: translateY(-25%);-webkit-transform: translateY(-25%);
	}
    /*.myconfirm .myconfirmk .myconfirm_main p:first-child{ padding: 1rem 1rem;;   font-size: 1.4rem; border-bottom:1px solid #ddd}
    */.myconfirm .myconfirmk .myconfirm_main p{padding: 30px 15px ; letter-spacing: 2px;   font-size: 14px; line-height: 20px; text-align: center;}
    .myconfirm .myconfirmk .myconfirm_main div{border-top:1px solid #eee;background: #f6f6f6; padding: 10px 20px; text-align: right;;}
    .myconfirm .myconfirmk .myconfirm_main div a{margin-right:0.5rem;border-radius:3px; display: inline-block; font-size: 14px;  padding: 10px 20px; color: #fff;}
    .myconfirm_qd{ background: url(../assets/bj.jpg);}
    .myconfirm_qx{ background: silver;}
</style>
<template>
	 <div class="myconfirm" v-show="show">
	 	<div class="myconfirmk">
	 		<div class="myconfirm_main">

	 			<p>{{mess}}</p>
	 			<div>
	 				<a @click="qx" class="myconfirm_qx">取消</a>
	 				<a @click="qd" class="myconfirm_qd">确定</a>
	 				
	 			</div>
	 		</div>
	 	</div>
	 </div>
</template>

<script>
export default{
	name:"myconfirm",
	props:['mess','show'],
	data(){
		return {
			
			
		}
	},
	methods:{
		qd(){
			this.$emit('confirmSure')
		},
		qx(){
			this.$emit('confirmCancel')
		}
	}
	
}
</script>




