<style>
	.search_top{ padding: 0.7rem 0.7rem; background: #fff;}
	.search_top_main{ display: flex; display: -webkit-flex; align-items: center; justify-content: center; padding: 0.5rem 0.5rem;  width:90%; border: 1px solid #ddd; margin: 0px auto; border-radius:2rem}
	.search_top_main img{ flex:none; width:2rem; margin-left: 0.5rem;}
	.search_top_main span{flex:none; font-size: 1.2rem; padding: 0rem 0.7rem; height: 1.5rem; line-height: 1.5rem; border-right:1px solid #ddd;}
	.search_top_main label{ font-size: 1.2rem;; flex:auto; height: 2rem; line-height: 2rem; border: none;margin-left: 0.5rem; font-size: 1.2rem;}
/**
 * 
 */

.dingfang_data{ position: absolute; padding: 0rem 0rem; width:92%; border-radius:5px;   height: auto; background: #fff; border: 1px solid #ddd;margin:0.7rem 4%; z-index: 20; overflow: hidden;}
    .dingfang_data_address{  padding: 1rem 1rem; border-bottom: 1px solid #ddd;
    display: flex; display: -webkit-flex; flex-flow:row nowrap; align-items: center;justify-content: center;
    }
    .dingfang_data_address span{ flex:auto; font-size: 1.4rem;}
    .dingfang_data_address img{ flex:none; width:1.5rem; margin-right: 0.5rem;}
    .dingfang_data_address label{ flex:none; font-size: 1.2rem; color:#303030;}
    
    .dingfang_data_time{padding: 1rem 1rem; border-bottom: 1px solid #ddd;
    display: flex; display: -webkit-flex; flex-flow:row nowrap; justify-content: space-between; font-size: 1.2rem;}
    .dingfang_data_time label{ color: gray; padding: 0.3rem 1.5rem; border:1px solid #ddd; border-radius:2rem;}
    .dingfang_data_time span{ padding: 0.3rem 0rem;}
    
    
    .dingfang_data_count{padding: 1rem 1rem; border-bottom: 1px solid #ddd;
    display: flex; display: -webkit-flex; flex-flow:row nowrap;align-items: center;justify-content: center;}
    .dingfang_data_count span{ font-size: 1.2rem; padding: 0.3rem 0rem;  flex:none;}
    .dingfang_data_count label{ flex:auto; margin-left: 0.5rem; display: flex; display: -webkit-flex; flex-flow:row nowrap; align-items: center; justify-content: center;}
    .dingfang_data_count label i{ font-style: normal; ; margin-right: 0.5rem;  width:2rem; height: 2rem; line-height: 2rem; text-align: center; font-size: 1.4rem; border: 1px solid #ddd; border-radius:50%;;}
     
     .dingfang_data_btn{padding: 1rem 1rem; border-bottom: 0px solid #ddd;
    display: flex; display: -webkit-flex; flex-flow:row nowrap;align-items: center;justify-content: space-around}
    .dingfang_data_btn a{ font-size: 1.4rem; padding:0.7rem 3rem; text-align: center; color: #fff; border-radius:3px;}
    .dingfang_data_btn a:first-child{ background: silver; }
      .dingfang_data_btn a:last-child{ background:#FF414F;}
      
      .renactive{ border: 1px solid #FF414F !important; color: #FF414F;}
      /*picker3*/
      .picker3{
      	padding-top:5rem;
      	 z-index: 21;
      	position:absolute; width:100%; height: 100%; background: rgba(0,0,0,0.3); top:100%; left: 0px;
        transition:0.2s all;
        -webkit-transform: 0.2s all;
      }
      .totop{top:0px !important;}
      /*city*/
</style>
<template>
  <div class="search">
      <div class="search_top">
      	  <div class="search_top_main">
      	  	  	  <img src="../assets/index/dw.png">
      	  	  	  <span>{{city}}</span>
      	  	      <label  @click="searchClick"> 搜索位置，时间，人数等</label>
      	  </div>
      </div>
      
      
      
      <div class="dingfang_data" v-show="searchShow">
			      	<div class="dingfang_data_address" @click="citysort" >
			      		<span>{{cityName}}</span>
			      		<img src="/static/img/dingfang/dw1.png">
			      		<label>我的位置</label>
			      	</div>
			      	<div class="dingfang_data_time" @click="getDate">
			      		<span>{{startD}}</span>
			      		<label>{{nights}}晚</label>
			      		<span>{{endD}}</span>
			      	</div>
			      	<div class="dingfang_data_count">
			      		<span>宜居人数</span>
			      		<label @click="selectCount">
			      			<i>1</i><i>2</i><i>3</i><i>4</i><i>5</i><i>6</i><i>7</i><i>8</i>
			      		</label>
			      	</div>
			      	<div class="dingfang_data_btn">
			      		<a class="btn " @click="qx">取消</a>
			      		<a class="btn " @click="searchStart">搜索</a>
			      	</div>
		      </div>
      
      
      <div ref="picker3" class="picker3" v-if="showpicker">
      	 <picker3 style="padding-bottom: 10rem;" @selectTime="startend" ></picker3>
      </div>
  </div>
</template>

<script>
import picker3 from '@/components/picker3.vue' 
 import HUB from "../assets/HUB.js"
export default{
		name:"search",
		components: {  
			picker3,
			
		},
		props:["city"],
		data(){
			return {
				showpicker:false,
				startD:"入住日期",
				endD:"退房日期",
				ren:1,//入住 人数
				cid:0,
				nights:0,//入住几晚
               cityName:"",
				searchShow:false,
			}
		},
		methods:{
			searchClick(){
				this.searchShow=true;
			},
			qx(){
				this.searchShow=false,
				this._qz();
			},
			_qz(){
				this.startD="入住日期";
				this.endD="退房日期";
				this.nights=0;
				this._cancelCount();
			},
			selectCount(event){
				var o=event.target;
				if(o.nodeName!="I"){return;}
				this._cancelCount();
				this.ren=o.innerHTML;
				o.setAttribute("class","renactive")
			},
			_cancelCount(){
				var objs=document.querySelectorAll(".dingfang_data_count label i")
				for(let i=0;i<objs.length;i++)
				{
					objs[i].setAttribute("class","")
				}
				this.ren=1
			},
			startend(p){
				if(!p)
				{
					this.showpicker=false;
					this.startD="入住日期";
					this.endD="退房日期";
					this.ren=1;//入住 人数
					this.cid=0;
					this.nights=0;//入住几晚
					return;
				}
				this.startD=p.startD;
				this.endD=p.endD;
				var x=new Date(this.startD).getTime()
	  		    var y=new Date(this.endD).getTime()
	  		    this.nights=((y-x)/1000/60/60/24)
				
				this.$refs.picker3.setAttribute("class","picker3")
				var that=this;
				setTimeout(function(){that.showpicker=false;},600)
			},
			
			getLocation(){
				
			},
			getDate(){
				this.showpicker=true;
				var that=this;
				setTimeout(function(){that.$refs.picker3.setAttribute("class","totop picker3")},200)
				
			},
			/*城市*/
			citysort(){
				this.$router.push({name:"citysort",params:{data:"searchcontent"}})
			},
			searchStart(){
				var p={
					city:this.cityName,
					startD:this.startD,
					endD:this.endD,
					cid:this.cid,
					night:this.nights,
					ren:this.ren,
				}
				this.searchShow=false;
				this.$emit("searchContent",p);
			}
		},
		mounted(){
			HUB.$on("ctiysort_tell_dingfang",(p)=>{
					// alert("hub="+p.name)
					 this.cityName=p.name;
					 this.cid=p.id;
				})
		},
		activated(){
			var that=this;
			setTimeout(function(){
				//alert("activated="+that.city)
			},2000)
		   //alert("搜索组件被选中并加载")	
		},
		watch:{
		'$route' (to, from) 
		   {
		   	console.log(to)
		   	console.log("searchsearchsearchsearchsearchsearchsearch")
		   	console.log(from)
			   	if(from.name=="Index")
			   	{
		
			   		    this.cityName=this.city;
			   	}
		   }
	}
}
</script>




