
<template>
	<div class="foot">
		<ul>
		   <li>
		   	   <router-link to="/#">
		   	      <img :src="foot1img">
		   	      <p :class="foot1">首页</p>
		   	   </router-link>
		   </li>
		   
		   <li >
		   	   <router-link to="/dingfang">
		   	      <img :src="foot2img">
		   	      <p :class="foot2">订房</p>
		   	   </router-link>
		   </li>
		   
		   <li>
		   	   <router-link to="/xingcheng">
		   	      <img :src="foot3img">
		   	      <p :class="foot3">行程</p>
		   	   </router-link>
		   </li>
		   
		   <li>
		   	   <router-link to="/my">
		   	      <img :src="foot4img">
		   	      <p :class="foot4">我的</p>
		   	   </router-link>
		   </li>
		   
		</ul>
	</div>
</template>

<script>
	export default{
		name:"foot",
		props:['pos'],
		
		data(){
			return {
				foot1img:"/static/img/foot/foot1.png",
				foot2img:"/static/img/foot/foot2.png",
				foot3img:"/static/img/foot/foot3.png",
				foot4img:"/static/img/foot/foot4.png",
				foot1:"",foot2:"",foot3:"",foot4:"",
				
			
			}
		},
		methods:{
			check(){
				this.footDef();
				
				switch (this.pos)
				{
					case  '1':
					  this.foot1img="/static/img/foot/foot1r.png";
					  this.foot1="footactive";
					break;
					case '2' :
					  this.foot2img="/static/img/foot/foot2r.png";
					  this.foot2="footactive";
					break;
					case '3' :
					   this.foot3img="/static/img/foot/foot3r.png";
					   this.foot3="footactive";
					break;
					case '4' :
					   this.foot4img="/static/img/foot/foot4r.png";
					   this.foot4="footactive";
					break;
				}
			},
			footDef(){
				this.foot1img="/static/img/foot/foot1.png";
				this.foot2img="/static/img/foot/foot2.png";
				this.foot3img="/static/img/foot/foot3.png";
				this.foot4img="/static/img/foot/foot4.png";
				this.foot1="";this.foot2="";this.foot3="";this.foot4="";
			}
		},
		created(){
			
		},
		mounted(){
			this.check()
			
		},
		watch:{
			pos:{
				handler(curVal,oldVal)
				{
					this.check()
                } 
           },
          
		},
			
		
	}
</script>

<style>
	.foot{  border-top:1px solid #f6f6f6; z-index: 5; background: #fff; width:100%; position: fixed; left: 0px; right: 0px; margin: 0 auto; bottom: 0;}
    .foot ul{ margin: 0; padding: 0.3rem 0rem; overflow: hidden; width:100%}
    .foot ul li{ width:25%; float:left; list-style-type: none; }
    .foot ul li a{display: flex; display: -webkit-flex; flex-flow:column; align-items: center; justify-content: center;}
    .foot ul li a img{ width:2rem}
    .foot ul li a p{ font-size: 1rem; margin-top: 0.3rem;}
    .foot ul li a .footactive{ color: #FF414F;}
</style>


