<style>
	.my_top{ background: url(../../static/img/my/mybj.png) no-repeat ; background-size: 100% 13rem;}
     .my_top_tx{ display: flex; display: -webkit-flex; padding: 4rem 1rem 2rem 1rem;;
     flex-flow:row nowrap;justify-content: center;align-items: center;
     }
     .my_top_tx img{ border: 1px solid #fff; width:5rem; height: 5rem; border-radius:50%; flex:none}
     .my_top_tx span{flex:auto; font-size: 1.6rem; color: #fff; margin-left: 1rem;}
     
     .my_main{; width:95%; height:auto; margin: 0px auto; background: #fff; border: 1px solid #eee ;}
     .my_main ul{ margin: 0; padding: 1rem;}
     .my_main ul li{ list-style-type: none; width:100%; padding: 1.5rem 0rem; border-bottom: 1px solid #f6f6f6;}
     .my_main ul li:last-child{ border-bottom: none;}
     .my_main ul li a{ display:flex; display: -webkit-flex;flex-flow:row nowrap; justify-content: center;align-items:center ;}
     .my_main ul li a img{ width:1.5rem;flex:none}
     .my_main ul li a span{ flex:auto; margin-left: 1rem; font-size: 1.2rem;}
</style>
<template>
	<div class="my" style="padding-bottom: 10rem;">
		<div class="my_top">
			<div class="my_top_tx" >
				<img :src="tx"/>
				<span>{{nc}}</span>
			</div>
			<div class="my_main">
				<ul>
					<li><a href="/index.php/weixin/member/getcoupon">
				    	<img src="/static/img/my/quan.png">
				    	<span>领取优惠券</span>
				    </a></li>
				    <li><a href="/index.php/weixin/member/mycoupon">
				    	<img src="/static/img/my/quan.png">
				    	<span>我的优惠券</span>
				    </a></li>
				    <li><a href="/index.php/weixin/member/mycollection">
				    	<img src="/static/img/my/sc.png">
				    	<span>我的收藏</span>
				    </a></li>
				    <li><a href="/index.php/weixin/member/apply">
				    	<img src="/static/img/my/fd.png">
				    	<span>成为房东</span>
				    </a></li>
				    <li><a href="/index.php/weixin/member/mydata">
				    	<img src="/static/img/my/myzl.png">
				    	<span>个人资料</span>
				    </a></li>
				    <li><router-link to="">
				    	<img src="/static/img/my/change.png">
				    	<span>切换房东模式</span>
				    </router-link></li>
				    <li><a href="/index.php/weixin/member/explain">
				    	<img src="/static/img/my/xy.png">
				    	<span>服务协议</span>
				    </a></li>
				    
				</ul>
			</div>
		</div>
		<!--<a @click="showalert">显示提示框 </a><br><br><a @click="showconfirm">显示提示框 </a>
		<myalert v-show="alert.show" @qd="alertqd"  :mess="alert.mess" ></myalert>
		<myconfirm v-show="confirm.show" @qd="confirmqd"  :mess="confirm.mess"></myconfirm>-->
	</div>
</template>

<script>
	import myalert from "@/components/alert.vue"
	import myconfirm from "@/components/confirm.vue"
export default{
	name:"my",
	components:{myalert,myconfirm},
	data(){
		return{
			tx:"",
			nc:"",
			alert:{
				show:false,
				mess:"这是提示内容"
			},
			confirm:{
				show:false,
				mess:"这是提示内容"
			},
			
		}
	},
	methods:{
		alertqd(res){this.alert.show=false;},
		confirmqd(res){this.confirm.show=false;},
		showalert(){
			
			this.alert={
				show:true,
				mess:"提交成功"
			}
		},
		showconfirm(){
			this.confirm={
				show:true,
				mess:"确定要放弃编辑吗?"
			}
		},
		getCookie(c_name)
		{
			if (document.cookie.length>0)
			{ 
			   var c_start=document.cookie.indexOf(c_name + "=")
				if (c_start!=-1)
				{ 
					c_start=c_start + c_name.length+1 
					var c_end=document.cookie.indexOf(";",c_start)
					if (c_end==-1) c_end=document.cookie.length
					return unescape(document.cookie.substring(c_start,c_end))
				} 
			}
			return ""
		}
	},
	mounted(){
		//alert(this.qbl.nickname)
		
	},
	 watch: {
		   '$route' (to, from) 
		   {
		   	console.log(from)
		   	console.log("*****************************")
		   	console.log(to);
		     //console.log(this.getStatus(this.$route.path))
		   }
    },
    activated(){
    	this.nc=this.getCookie("nickname")
    	this.tx=this.getCookie("headimgurl")
    	//alert(this.nc)
    }
	
}
</script>



