<style type="text/css">

	input{-webkit-user-select:auto;}
	.index{ background: #fff;}
	.zmd{ width:100%}
	
	.swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets{ bottom:0rem}
	.swiper-pagination-bullet-active{ background: url(../assets/bj.jpg) !important;}
	.swiper-pagination-bullet{ background: url(../assets/bj.jpg) !important;}
	.swiperk{padding: 0rem 0.7rem;}
	/*index_top*/
	.index_top{background: #fff; display: flex; display: -webkit-flex; padding: 0.7rem 0.7rem; justify-content: center; align-items: center;}
	.index_top .index_top_one{ flex:none; width:2rem}
	.index_top .index_top_two{ flex:none; margin-left: 0.5rem; font-size: 1.2rem;}
	.index_top .index_top_three{ border: 1px solid #ddd; flex:auto; padding: 0.5rem 0rem; border-radius:1rem; margin-left: 0.5rem; display: flex; display: -webkit-flex;justify-content: center; align-items: center;}
	.index_top .index_top_three img{ flex:none; margin-left: 1rem; width:1.5rem}
	.index_top .index_top_three span{  flex:auto; border: none; margin-left: 1rem; font-size: 1.2rem;}
	.index_top .index_top_four{ flex:none; width:2rem;margin-left: 0.5rem;}
	/*分段*/
	.fd{ margin-top: 1rem; display: flex; display: -webkit-flex;padding: 0.7rem 0.7rem; justify-content: center; align-items: center;}
	.fd span{flex:none; width:0.5rem; height: 1.7rem; background:url(../assets/bj.jpg); border-radius:0.5rem;}
	.fd label{flex:auto; margin-left: 0.7rem; font-size: 1.4rem;}
	.fd a{ color: gray; font-size: 1rem;}
	
	/**/
	.splistk{ padding: 0rem 0.7rem 5rem 0.7rem;}
</style>
<template>

  <div class="index">
    
  	 <div class="index_top">
  	 	  <img src="../assets/index/dw.png" class="index_top_one">
  	 	  <span class="index_top_two" >{{city}}</span>
  	 	  <div class="index_top_three" @click="tosearch" >
  	 	  	  <img src="../assets/index/ss.png">
  	 	  	  <span >搜索</span>
  	 	  </div>
  	 	   <img src="../assets/index/xx.png"  class="index_top_four">
  	 </div>
      <!--这是首页{{msg}}
	   <div @click="aaa">aaa</div>
	   <router-link to="/a/001/8081">跑到A页</router-link>
	   <br>
	   <router-link to="/scroll">到下拉加载页面</router-link>
	   <br>
	   <router-link to="/swiper">轮播</router-link>
	   <br>
		 
	   <router-link to="/city">三级连动</router-link>
	  
	   <br>
	   <router-link to="/picker2">开始至结速</router-link>
	   <br>
	    <router-link to="/picker3">PICKER3</router-link>
	   <br>
	     <img src="../assets/logo.png"/>
	   <br>
	   <a href="hbuilder://">cccccccccccddddddd6666</a>
	   
	   
	   <a href="javascript:window.location.href='weixin://'">点击这里打开微信</a><br></br>-->
	   
	   	<swiper :options="swiperOption"  ref="mySwiper" style="padding-bottom:2rem !important; ">  
                
								<swiper-slide v-for="item in swiper1"  style=" background:#fff" >
									<a :href="item.url">
										<img class="zmd" :src="item.thumb">
								    </a>
								</swiper-slide> 
							 <!--<swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/zmd.png"></router-link> </swiper-slide>  
								<swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/zmd.png"></router-link> </swiper-slide>  
								<swiper-slide  style="background:#fff" ><router-link to=""><img class="zmd" src="../assets/zmd.png"></router-link> </swiper-slide>  
                               -->
           
            <div class="swiper-pagination" slot="pagination"></div>  
      </swiper> 
      <div class="fd"><span></span><label>热门活动</label><router-link to="">更多</router-link></div>
      <div  class="swiperk">
		    <swiper :options="swiperOption2"  ref="mySwiper2">  
									<swiper-slide v-for="item in swiper2"  style=" background:#fff" >
										<router-link to="">
											<img class="zmd" :src="item.cover">
										</router-link> 
									</swiper-slide>  
								<!-- <swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/S80602-151709.png"></router-link> </swiper-slide>  
								 <swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/S80602-151704.png"></router-link> </swiper-slide>  
								 <swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/S80602-151709.png"></router-link> </swiper-slide>  
	                           -->
	      </swiper>
      </div>
      <div class="fd"><span></span><label>热门城市</label><router-link to="">更多</router-link></div>
      <div  class="swiperk">
      	

	      <swiper :options="swiperOption3"  ref="mySwiper3">  
	                              <!--<swiper-slide   style=" background:#fff" >
										<router-link to="/hotcity/20/济南">
											<img class="zmd"  src="../../static/img/index/S80602-151704.png">
										</router-link> 
									</swiper-slide> -->
									<swiper-slide v-for="item in swiper3"  style=" background:#fff" >
										<router-link :to="item | hotcityurl">
											<img class="zmd"  :src="item.thumb">
										</router-link> 
									</swiper-slide> 
									<!--<swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/组20拷贝2.png"></router-link> </swiper-slide> 
									<swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/组20.png"></router-link> </swiper-slide>  
									<swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/组20拷贝2.png"></router-link> </swiper-slide>  
									<swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/组20.png"></router-link> </swiper-slide>  
									<swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/组20拷贝2.png"></router-link> </swiper-slide>  
									<swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/组20.png"></router-link> </swiper-slide>  
									<swiper-slide  style=" background:#fff" ><router-link to=""><img class="zmd" src="../assets/index/组20拷贝2.png"></router-link> </swiper-slide>  
							-->
	      </swiper>
       </div>
      <div class="fd"><span></span><label>发现最美的家</label><router-link to="">更多</router-link></div>
      <div class="splistk">
         <splist :list="sp"></splist>
      </div>
	 <wait :show="waitShow"></wait>
	 <myalert :mess="alertMess" :show="alertSshow"></myalert>
  </div>
  
</template>

<script>
 import { swiper, swiperSlide } from 'vue-awesome-swiper' 
 import splist from '@/components/splist.vue' 
 import wait from '@/components/wait.vue'
 import myalert from '@/components/alert.vue'

 export default{
  name: 'Index',
  components: {  
            swiper,  
            swiperSlide ,
            splist,
            wait,
            myalert,
          
           
        }, 
    filters:{
    	hotcityurl(o)
    	{
    		return "/hotcity/"+o.id+"/"+o.title;
    	}
    },
	data () {
    return {  
                swiperOption: {  
                //是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true  
		                notNextTick: true,  
						        autoplay:true,
						        speed:300,
		                slidesPerView: 'auto',  
		                pagination: {
							        el: '.swiper-pagination',
							        clickable: true,
							      }
                } ,
                swiperOption2:{
                	  slidesPerView: 2.5,
							      spaceBetween: 20,
							      freeMode: true,
							      loop:true
							      
                },
                swiperOption3:{
                	  slidesPerView: 5,
							      spaceBetween: 10,
							      freeMode: true,
							      loop:true
							      
                },
                cc:[
                  {"houseimg":"/static/img/index/index1.png","title":"发现最美的家家家家","src":"/static/img/index/tx1.png","name":"房东名称"},
                  {"houseimg":"/static/img/index/index2.png","title":"发现最美的发现最美","src":"/static/img/index/tx2.png","name":"房东名称"},
                  {"houseimg":"/static/img/index/index1.png","title":"发现最美的发现最美","src":"/static/img/index/tx1.png","name":"房东名称"},
                  {"houseimg":"/static/img/index/index2.png","title":"发现最美的发现最美","src":"/static/img/index/tx2.png","name":"房东名称"},

                ],
                swiper1:[],
                swiper2:[],
                swiper3:[],
                sp:[],
                city:"",
                waitShow:true,
                alertSshow:false,
                alertMess:"网络异常",
            }  
  },
   methods:{
    // 	swiper() {  return this.$refs.mySwiper.swiper;  } ,
	   aaa:function()
	   {
	      this.$router.go(-1)
	     // this.$ajax.get("/minsu/tp/public/index.php/index/index/ajax",{id:5}).then(function(res){alert(res)}).catch(function(er){alert(er)})
	   },
	  
	   tosearch(){this.$router.push({name:"indexsearch",params:{data:"searchcontent"}})},
	   pageInit(){
	   	var that=this;
	   	
	   	   this.$ajax.post(
	   	   	'http://minsu.youfai.cn/index.php/weixin/index/getindex',
	   	   	{city:that.city}).then(function(res){
	   	   		that.waitShow=false;
	   	   		//alert(JSON.stringify(res))
	   	   		that.swiper1=res.data.slide;
	   	   		that.swiper2=res.data.activity;
	   	   		that.swiper3=res.data.label;
	   	   		that.sp=res.data.list;
	   	   		//that.qbl=res.data.member;
	   	   		/*that.qbl.nickname=res.data.member.nickname;
	   	   		that.qbl.headimgurl=res.data.member.headimgurl;
	   	   		localStorage.nickname=res.data.member.nickname;
	   	   		localStorage.headimgurl=res.data.member.headimgurl;*/
	   	   		that.setCookie("nickname",res.data.member.nickname,365)
	   	   		that.setCookie("headimgurl",res.data.member.headimgurl,365)
	   	   		//alert("COOK置入成功")
	   	   		
	   	   	}).catch(function(er){that.waitShow=false;})
	   },
	   setCookie(c_name,value,expiredays)
		{
			var exdate=new Date()
			exdate.setDate(exdate.getDate()+expiredays)
			document.cookie=c_name+ "=" +escape(value)+
			((expiredays==null) ? "" : "; expires="+exdate.toGMTString())
		},
	   /*
	    * 自动获取用户位置
	    */
	   getPosition(){
	   	    var that=this;
	   	    var geolocation = new qq.maps.Geolocation("ASOBZ-U2RK6-PZQSM-ESQWD-7XO3J-TAFF3", "aa");
		    	    geolocation.getLocation(function(p){
		    	    	
		    	    	
		    	    //alert(JSON.stringify(p))
		    	    that.qbl.lat=p.lat;
		    	    that.qbl.lng=p.lng;
		    	    that.qbl.city=p.city;
		    	    that.city=p.city.replace("市","");

		    	    localStorage.lat=p.lat;
		    	    localStorage.lng=p.lng;
		    	    localStorage.city=p.city;
    
		    })
	   }
	   
	   
	},
	mounted(){
		
		this.getPosition()
		this.pageInit()
		
		
		
		
	  // this.swiper.slideTo(0, 0, false);  
	 
	}
	
 }
</script>


