<style>
	.myalert{
		position:fixed; width:100%; height: 100%; background: rgba(0,0,0,0.5); z-index: 15; top:0px; left: 0px;
	}
	.myalert .myalertk{ width:100%; height: 100%; background: none;}
	.myalert .myalertk .myalert_main{ max-width: 400px; border-radius:5px; overflow: hidden;; width:80%; height: auto; background: #fff; margin: 0px auto; margin-top: 50%;
	transform: translateY(-25%);-webkit-transform: translateY(-25%);
	}
    /*.myalert .myalertk .myalert_main p:first-child{ padding: 1rem 1rem;;   font-size: 1.4rem; border-bottom:1px solid #ddd}
    */.myalert .myalertk .myalert_main p{padding: 30px 15px ; letter-spacing: 2px;   font-size: 14px; line-height: 20px; text-align: center;}
    .myalert .myalertk .myalert_main div{border-top:1px solid #eee;background: #f6f6f6; padding: 10px 20px; text-align: right;;}
    .myalert .myalertk .myalert_main div a{margin-right:0.5rem;border-radius:3px; display: inline-block; font-size: 14px; background: url(../assets/bj.jpg); padding: 10px 20px; color: #fff;}
</style>
<template>
	 <div class="myalert" v-show="show">
	 	<div class="myalertk">
	 		<div class="myalert_main">
	 			<!--<p>{{title}}</p>-->
	 			<p>{{mess}}</p>
	 			<div><a @click="qd">确定</a></div>
	 		</div>
	 	</div>
	 </div>
</template>

<script>
export default{
	name:"myalert",
	props:['mess','show'],
	data(){
		return {
			mess:"这里是提示信息",
		}
	},
	methods:{
		qd(){
			this.$emit('qd','1')
		}
	}
	
}
</script>




