<template>
	<div class="none" v-show="show">
		<div class="nonek">
		<img src="../../static/img/none.png">
		</div>
	</div>
</template>

<script>
	export default{
		name:"none",
		props:['show'],
		data(){
			return {
				
			}
		}
		
		
	}
</script>

<style>
	.none{ z-index: 0;; position:fixed; top:4rem; left: 0px; right: 0px;   width:100%; height: 100%; }
	.nonek {width:100%; height: 100%; position: relative;}
	.nonek img{ position: absolute; left: 0px; right: 0px; top:50%; width:10rem; margin: auto;
	transform:translateY(-70%);-webkit-transform:translateY(-70%);
	}
</style>


