<template>
  <div>
     router
	 <div>当前传来的ID：{{id}}</div>
    <router-link to="/">到首页</router-link>
	<button @click="changeid">aaa</button>
	
	
	<div style="padding:20px 20px"><a href="http://www.baidu.com">这是A链接，可跳出去</a></div>
	    
	         <div class="wrapper" ref="wrapper" style="background:#fff; height:600px; overflow:hidden;">
			  <ul class="content">
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li><li style="height:100px; line-height:100px">选 项</li><li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				<li style="height:100px; line-height:100px">选 项</li>
				
			  </ul>
			  <!-- 这里可以放一些其它的 DOM，但不会影响滚动 -->
			</div>
	   
	
	
	
  </div>
 
</template>
<script>
import BScroll from 'better-scroll'
export default {
  name: 'Myrouter',
  data(){
       return{
	   id:"99",
	   src:"",
	}
  },
  methods:{
   changeid(){this.id=999;}
  },
  mounted(){
    this.$nextTick(()=>{
	   this.scroll = new BScroll(this.$refs.wrapper, {})
            this.scroll.on('scrollEnd', (pos) => {
		    if(pos.y==this.scroll.maxScrollY)
			{
			   alert("over")
			}
			console.log(this.scroll.maxScrollY)
			     console.log(pos)
			})
	})
	
    this.id=this.$route.params.id;
	
	
	//alert(this.$route.params.post)
  },
  watch:{
	 /* id(val,oldval){
		 alert(oldval+"变为"+val);
	  },*/
      
  }
 
 
}
</script>


