<style>
	.dingfang_main{background: url(../assets/dingfang/bj.png) no-repeat; background-size: 100%; padding-top: 4rem; }
	
	.dingfang_data1{border-radius:5px; padding: 0rem 1rem;  width:85%; height: auto; background: #fff; border: 1px solid #ddd;margin:0 auto;margin-top: 4rem; z-index: 1000; overflow: hidden;}
    .dingfang_data_address{  padding: 1rem 0rem; border-bottom: 1px solid #ddd;
    display: flex; display: -webkit-flex; flex-flow:row nowrap; align-items: center;justify-content: center;
    }
    .dingfang_data_address span{ flex:auto; font-size: 1.4rem;}
    .dingfang_data_address img{ flex:none; width:1.5rem; margin-right: 0.5rem;}
    .dingfang_data_address label{ flex:none; font-size: 1.2rem; color:#303030;}
    
    .dingfang_data_time{padding: 1rem 0rem; border-bottom: 1px solid #ddd;
    display: flex; display: -webkit-flex; flex-flow:row nowrap; justify-content: space-between; font-size: 1.2rem;}
    .dingfang_data_time label{ color: gray; padding: 0.3rem 1.5rem; border:1px solid #ddd; border-radius:2rem;}
    .dingfang_data_time span{ padding: 0.3rem 0rem;}
    
    
    .dingfang_data_count{padding: 1rem 0rem; border-bottom: 1px solid #ddd;
    display: flex; display: -webkit-flex; flex-flow:row nowrap;align-items: center;justify-content: center;}
    .dingfang_data_count span{ font-size: 1.2rem; padding: 0.3rem 0rem;  flex:none;}
    .dingfang_data_count label{ flex:auto; margin-left: 0.5rem; display: flex; display: -webkit-flex; flex-flow:row nowrap; align-items: center; justify-content: center;}
    .dingfang_data_count label i{ font-style: normal; ; margin-right: 0.5rem;  width:2rem; height: 2rem; line-height: 2rem; text-align: center; font-size: 1.4rem; border: 1px solid #ddd; border-radius:50%;;}
     
     .dingfang_data_btn{padding: 1rem 0rem; border-bottom: 0px solid #ddd;
    display: flex; display: -webkit-flex; flex-flow:row nowrap;align-items: center;justify-content: space-around}
    .dingfang_data_btn a{ font-size: 1.4rem; padding:0.7rem 3rem; text-align: center; color: #fff; border-radius:3px;}
    .dingfang_data_btn a:first-child{ background: silver; }
      .dingfang_data_btn a:last-child{ background:#FF414F;}
      
      .renactive{ border: 1px solid #FF414F !important; color: #FF414F;}
      
      /*splist*/
     .dingfang_splist ul{ margin: 0; padding: 0; overflow: hidden;padding: 0rem 1rem 7rem 1rem;  background: #fff;}
     .dingfang_splist ul li{ background: #fff; width:100%; float:left;  height:auto;  }
     .dingfang_splist ul li a{ background: #fff;; display: block;  overflow: hidden;}
     /*.dingfang_splist ul li:nth-child(2n) a{background: #fff; display: block; margin: 10px 10px 0px 5px;  overflow: hidden;;}
     */.dingfang_splist ul li a  .sptp{ width:100%; ;height:40vw  }
      .dingfang_splist ul  a div:first-child {height:40vw;overflow: hidden;}
      .dingfang_splist ul  a div:last-child{ padding: 0.7rem 0rem;}
      .dingfang_splist ul  a div:last-child p{ margin: 0 auto; padding-top: 0.5rem; display: flex; display: -webkit-flex; flex-flow:row nowrap; justify-content: center;; align-items: center;}
      .dingfang_splist ul  a div:last-child p:last-child span{ font-size: 1rem; flex:auto;}
      .dingfang_splist ul  a div:last-child p img{ width:1.3rem; height: auto; flex:none; margin-right:0.3rem;}
      .dingfang_splist ul  a div:last-child p label{ flex:none; font-size: 1rem;}
      .dingfang_splist ul  a div:last-child p:first-child span{ font-size: 1.2rem;flex:auto; font-weight: 400;}
  /*picker3*/
      .picker3{
      	padding-top:5rem;
      	 z-index: 6;
      	position:absolute; width:100%; height: 100%; background: rgba(0,0,0,0.3); top:100%; left: 0px;
        transition:0.2s all; -webkit-transition:0.2s all;
      }
      .totop{top:0px !important;}
      /*city*/
     .city{ position:absolute; top:0px; left: 0px; width:100%; height: 100%; background: rgba(0,0,0,0.3); z-index: 15; overflow-y: scroll;}

</style>
<template>
  <div class="dingfang">
      <div class="dingfang_main">
      
		      <div class="dingfang_data1">
			      	<div class="dingfang_data_address" @click="citysort" >
			      		<span>{{city}}</span>
			      		<img src="/static/img/dingfang/dw1.png">
			      		<label>我的位置</label>
			      	</div>
			      	<div class="dingfang_data_time" @click="getDate">
			      		<span>{{startD}}</span>
			      		<label>{{nights}}晚</label>
			      		<span>{{endD}}</span>
			      	</div>
			      	<div class="dingfang_data_count">
			      		<span>宜居人数</span>
			      		<label @click="selectCount">
			      			<i>1</i><i>2</i><i>3</i><i>4</i><i>5</i><i>6</i><i>7</i><i>8</i>
			      		</label>
			      	</div>
			      	<div class="dingfang_data_btn">
			      		<a class="btn " @click="qz">重置</a>
			      		<a class="btn " @click="search">搜索</a>
			      	</div>
		      </div>
      </div>
      
      <div ref="picker3" class="picker3" v-if="showpicker">
      	 <picker3 style="padding-bottom: 10rem;" @selectTime="startend" ></picker3>
      </div>
      
    
      
      
      
      <swiper :options="swiperOption"  ref="mySwiper2" style="padding:1.5rem 0rem; background: #fff; margin-top: 1rem;">  
      	
      	
      	
			<swiper-slide v-for="item in splist"   style=" background:#fff; text-align: center; font-size: 1.2rem;" >
				<span data-id="item.id">{{item.title}}</span>
			</swiper-slide> 
			<!--<swiper-slide   style=" background:#fff ; text-align: center;font-size: 1.2rem;" ><span>执门</span></swiper-slide>--> 
			
								
	  </swiper>
      <div class="dingfang_splist">
        <ul v-for="item in splist">
        	<li v-for="item2 in item.zz">
        		<a href="">
        			<div><img class="sptp" :src="item2.picture"></div>
        			<div>
        				<p><span>{{item2.title}}</span><label>￥{{item2.price}}/晚</label></p>
        				<p :data-lat="item2.latitude"  :data-lon="longitude"><img src="../assets/index/dw.png"><span>{{item2.qu}}{{item2.address}}</span></p>
        			</div>
        		</a>
        	</li>
        </ul>
      </div>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper' 
import picker3 from '@/components/picker3.vue' 
import HUB from "../assets/HUB.js"
export default{
		name:"dingfang",
		components: {  
			picker3,
			swiper,  
            swiperSlide ,
	    },
		data(){
			return {
				showpicker:false,
				startD:"入住日期",
				endD:"退房日期",
				ren:1,//入住 人数
				nights:0,//入住几晚
				cid:0,
				swiperOption:{
					 slidesPerView: 5.5,
					      spaceBetween: 0,
					      slidesPerGroup: 5,
					      loop: true,
					      loopFillGroupWithBlank: true,
					      
				},
				/*city*/
				city:"济南",
				splist:[],
			}
		},
		methods:{
			getPosition()
			{
		   	    var t=this;
		   	    var geolocation = new qq.maps.Geolocation("ASOBZ-U2RK6-PZQSM-ESQWD-7XO3J-TAFF3", "aa");
			    	    geolocation.getLocation(function(p){
			    	    t.city=p.city.replace("市","");
			    })
		    },
			qz(){
				this.startD="入住日期";
				this.endD="退房日期";
				this.nights=0;
				this._cancelCount();
			},
			selectCount(event){
				var o=event.target;
				if(o.nodeName!="I"){return;}
				this._cancelCount();
				this.ren=o.innerHTML;
				o.setAttribute("class","renactive")
			},
			_cancelCount(){
				var objs=document.querySelectorAll(".dingfang_data_count label i")
				for(let i=0;i<objs.length;i++)
				{
					objs[i].setAttribute("class","")
				}
				this.ren=1
			},
			startend(p){
				if(!p)
				{
					this.showpicker=false;
					this.startD="入住日期";
					this.endD="退房日期";
					this.ren=1;//入住 人数
					this.nights=0;//入住几晚
					return;
				}
				this.startD=p.startD;
				this.endD=p.endD;
				var x=new Date(this.startD).getTime()
	  		    var y=new Date(this.endD).getTime()
	  		    this.nights=((y-x)/1000/60/60/24)
				
				this.$refs.picker3.setAttribute("class","picker3")
				var that=this;
				setTimeout(function(){that.showpicker=false;},500)
			},
			getLocation(){
			},
			getDate(){
				this.showpicker=true;
				var that=this;
				setTimeout(function(){that.$refs.picker3.setAttribute("class","totop picker3")},200)
				
			},
			/*城市*/
			citysort(){
				this.$router.push({name:"citysort",params:{data:"searchcontent"}})
			},
			tomap(){
				this.$router.push({name:"map",params:{data:"searchcontent"}})
			},
			search(){
				var t=this;
				var p={
					startD:this.startD,
					endD:this.endD,
					ren:this.ren,
					city:this.city,
					cid:this.cid
				}
				console.log(p)
				this.$ajax.post(
		   	   	'/index.php/weixin/index/getroomlist',
		   	   p).then(function(res){
		   	   		console.log("getroomlist")
		   	   		
		   	   		console.log(res.data.list)
		   	   		t.splist=res.list
		   	   		console.log(t.data.splist)
		   	   		
		   	   	}).catch(function(er){that.waitShow=false;})
			},
			
		},
		created(){
		},
		mounted(){
			HUB.$on("ctiysort_tell_dingfang",(p)=>{
					this.city=p.name;
					this.cid=p.id;
			})
		},
		activated(){
		   //alert("订房组件被选中并加载")	
		   
		}
		
}

</script>




