<template> 
  
    <div style="width:100%; height:100%; background#fff; overflow-Y:scroll">
	    
	   <v-distpicker v-show="show" type="mobile" @selected="onSelected" @province="onChangeProvince" @city="onChangeCity"  ></v-distpicker>
	</div>
</template>
<script>
import VDistpicker from 'v-distpicker'

export default {
  name:"city",
  components: { VDistpicker },
  data(){
  	return {
  	  show:true,
  	  pro:"",//省份
  	  city:"",//城市
  	}
  },
  methods: {
    onSelected(data) {
		console.log(data)
	     // alert(data.province.value + ' | ' + data.city.value + ' | ' + data.area.value)
	     // console.log(data)
    },
    onChangeProvince(a){
    	  this.pro=a.value;
                            
            },
            onChangeCity(a){
               this.city=a.value 
                 this.show=false
            },
            onChangeArea(a){
                console.log(a)    
                this.show=false
            }     
  
    
  }
}
</script>

<style type="text/css">
	   /*遮罩层*/.blacks { position: fixed; width: 100%; height: 50%; left: 0; top: 0; background: rgba(0, 0, 0, 0.45);}

/*省市区三级联动*/

.divwrap {

  height: 50%;

  position: fixed;

  left: 0;

  bottom: 0;

  width: 100%;

  z-index: 99;

}

/*外部*/

.divwrap > .distpicker-address-wrapper {

  color: #0d0d0d;

  height: 100%;

}

/*头部*/

.divwrap >>> .address-header {

  background: #000;

  color: #fff;

  width: 100%;

  position: fixed;

  bottom: 50%;

  height: 0.5rem;

  font-size: 0.2rem;

}

/*头部内容*/

.divwrap >>> .address-header ul li {

  flex-grow: 1;

  text-align: center;

}

/*选择过省市区的头部*/

.divwrap >>> .address-header .active {

  color: #fff;

  border-bottom: 0.05rem solid #666;

}

/*内容部分*/

.divwrap >>> .address-container {

  overflow: scroll;

  height: 100%;

}

/*点过的地区内容*/

.divwrap >>> .address-container .active {

  color: red;

}


</style>


