<template>
   <div class="jiange" id="jiange" >
   	  <div ref="jiange" v-html="picker" @click="dateclick"  >
	   	  	<!--<div class="datetime_main">
	           <div class="datetime_top"><span class="prev"   ><</span>2018年8月<span class="next"   ><</span></div>
	           <div class="datetime_week"><table><tr><td>一</td><td>二</td><td>三</td><td>四</td><td>五</td><td>六</td><td>日</td></tr></table></table></div>
	           <div class="datetime_content">
	           	  <ul>
	           	  	<li></li><li></li><li></li>
	           	  	<li data-id="2018/02/01" data-total="100">
	           	  		<p>1</p>
	           	  		<p class="datetime_zt">状态</p>
	           	  		<p class="datetime_total">100</p>
	           	  	</li>
	           	  	
	           	  </ul>
	           </div>-->
	       </div>
   	  </div>
  </div>
</template>
<style>
   	 .jiange{ width:100%; }
   	 .datetime_main{ width:100%; overflow: hidden;background: #fff; padding-top: 1rem;}
   	 .datetime_top{ text-align: center; font-size: 1.4rem;}
   	 .datetime_top .prev{ display: inline-block; margin-right: 2rem; width:2rem}
   	 .datetime_top .next{ display: inline-block; margin-left: 2rem; width:2rem}
   	 
   	 .datetime_week{ }
   	 .datetime_week table{width:100%}
   	 .datetime_week table tr{ width:100%;display: flex;  display: -webkit-flex;flex-flow:row nowrap; align-items: center;;justify-content: center;}
     .datetime_week table tr td{ flex:auto; text-align: center; padding: 0.7rem 0rem; color: gray;}
     
     .datetime_content{ width:100%;}
   	 .datetime_content ul{ margin: 0; padding: 0; overflow: hidden;}
   	 .datetime_content ul li{ width:14.2%; float:left; text-align: center; overflow: hidden; min-height: 1px; padding: 10px 0px;}
     .datetime_date{ font-size: 1.4rem;}
     .datetime_total{ font-size:1rem; color: #FF414F; height: 1rem; line-height: 1rem;}
     .err  .datetime_date{ color: gray;}
     
     .datetime_content .active{background: #FF414F;  overflow: hidden;;}
     .datetime_content .active .datetime_date{ color: #fff;}
     .datetime_content .active .datetime_total{ color: #fff;}
     
     
     .datetime_content .startactive{ background:#FF414F ;border-radius:0.5rem 0 0 0.5rem}
     .datetime_content .endactive{ background:#FF414F ;border-radius:0 0.5rem 0.5rem 0}
     .datetime_content .lyactive{background:#FF414F;}
     
</style>

<script>
	

export default {
  name:"pick2",
  data () {
    return {
      picker:"",
      startO:null,// 开始对象
      endO:null,//结速对象
      startD:"",//开始日期
      endD:"",//结束日期
    }
  },
  methods:{
	  	dateclick(event){
	  		var o=event.target;//前进后退被点击
	  		if(this._changeMonth(o)){return}//处 理前进后退，并退出
	  		o=o.parentNode;//日期被点击
	  		if(o.classList.contains('err') || o.nodeName!="LI"){return;}
	  		
	  		if(!this.startO){this._createstartO(o);return;}//不存在初始值，则创建
	  		if(o.getAttribute("data-id")==this.startD){this._cancelSelect(o,'all')}//再次点开始，取消全部选择
	  		if(!this.endO){
	  			//是否比开始晚，早于开始，刚互换
	  			this._createendO(o)
	  			return;
	  			
	  		}
	  		{
	  			//alert("有了结速点")
	  			/*if(o.classList.contains('active') || o.classList.contains('endactive'))
	  			{
	  				this._cancelSelect(o,'all')
	  			}*/
	  			this._cancelSelect(o,'all');
	  			if(!(o.classList.contains('err'))){
	  				this._createstartO(o)
	  			}
	  		}
	  	},
	  	/*创建结束点*/
	  	_createendO(o){
	  		var d=o.getAttribute("data-id");
	  		if(new Date(this.startD) > new Date(d))
	  		{
	  			var middleO=this.startO,middleD=this.startD;
	  			if(this._ableSelect(o.nextElementSibling,middleD))
	  			{

	  				this._createstartO(o)
	  			    this._createendO2(middleO)
	  			    this._select(this.startO)
	  			}
	  			else
	  			{
	  				
	  			}
	  		}
	  		else
	  		{
	  			if(this._ableSelect(this.startO.nextElementSibling,d))
	  			{
	  				 this._createendO2(o)
	  				this._select(this.startO)
	  			}
	  			else
	  			{
	  				
	  			}
	  			
	  		}
	  	},
	  	_createendO2(o){
	  		this.endD=o.getAttribute("data-id");this.endO=o;
	  		var classList=o.classList+" active endactive"
	  		o.setAttribute("class",classList);
	  	},
	  	/*select*/
	  	_select(o){
	  		//alert(this.startD+"*/*****"+this.endD)
	  		o=o.nextElementSibling;
	  		if(o.getAttribute("data-id")==this.endD){return;}
	  		o.setAttribute("class","li succ active");
	  		return this._select(o);
	  	},
	  	/*检测是否可多选*/
	  	_ableSelect(o,endD){
	  		
	  		if(o.classList.contains('err')){return false;}
	  		
	  		if(o.getAttribute("data-id")==endD){return true;}
	  		var vo=o.nextElementSibling;
	  		return this._ableSelect(vo,endD);
	  	},
	  	/*取消点或全部*/
	  	_cancelSelect(o,s){
	  		
	  		if(s=="all")
	  		{
	  			var objs=document.querySelectorAll(".active");
	  			for(let i=0;i<objs.length;i++){objs[i].setAttribute("class","li succ");}
	  			this.startO=null;this.startD="";this.endO=null;this.endD="";
	  		}
	  		else
	  		{
	  			o.setAttribute("class","li succ");
	  		}
	  	},
	  	/*创建开始点*/
	  	_createstartO(o){
	  		this.startO=o;
	  		this.startD=o.getAttribute("data-id");
	  		var classList=o.classList+" active startactive"
	  		o.setAttribute("class",classList);
	  	},
	  	/*前进后退*/
	  	_changeMonth(o){
	  		if(o.classList.contains('prev')){
	  			if(!o.parentNode.parentNode.previousElementSibling){return;}
	  			o.parentNode.parentNode.style.display="none";
	  			o.parentNode.parentNode.previousElementSibling.style.display="block";return true;
	  		}
	  		if(o.classList.contains('next')){
	  			if(!o.parentNode.parentNode.nextElementSibling){return;}
	  			o.parentNode.parentNode.style.display="none";
	  			o.parentNode.parentNode.nextElementSibling.style.display="block";return true;
	  		}
	  		return false;
	  	},
  	
  },/*methodsEND*/
  created(){
 var j={"2018":{"6":{"10":{"total":"200","type":"0","time":{"s1s":"8","s1e":"9","s1t":"80%","s2s":"8","s2e":"9","s2t":"60%","s3s":"null","s3e":"null","s3t":"null","s4s":"null","s4e":"null","s4t":"null"}},"11":{"total":"200","type":"1","time":{"s1s":"8","s1e":"9","s1t":"80%","s2s":"8","s2e":"9","s2t":"60%","s3s":"null","s3e":"null","s3t":"null","s4s":"null","s4e":"null","s4t":"null"}},"12":{"total":"200","type":"1","time":{"s1s":"8","s1e":"9","s1t":"80%","s2s":"8","s2e":"9","s2t":"60%","s3s":"null","s3e":"null","s3t":"null","s4s":"null","s4e":"null","s4t":"null"}},"13":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"14":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"15":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"16":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"17":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"19":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"20":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"21":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"22":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"23":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}},"24":{"total":"500","type":"1","time":{"s1s":"1","s1e":"1","s1t":"","s2s":"1","s2e":"1","s2t":"","s3s":"1","s3e":"1","s3t":"","s4s":"1","s4e":"1","s4t":""}}}}}
	var t={
	   start:"",
	   end:"",
	   year:(new Date()).getFullYear(),
	   month:(new Date()).getMonth()+1,
	   day:(new Date()).getDate(),
	   isRyear:()=>{if(  (t.year%4==0 && t.year%100!=0) || (t.year%400==0 && t.year%100!=0)  ){return true}return false;},
	   is30:(m)=>{if(/4|6|9|11/g.test(m)){return true;}else{return false;}},
	   insert:()=>{
	   	var month=t.month+6
	   	  for(let i=t.month;i<month+6;i++)
	   	  {
	   	  	    
		   	  	if(t.month>12)
		   	  	{
		   	  		t.year=parseInt(t.year)+1;
		   	  		t.month=i%12
		   	  	}
		   	  	var dom_top="";
		   	  	var dom_bottom=`
			   	  	      </ul>
			           </div>
			       </div>
			   	`
		   	  	
		   	  	if(t.month==(new Date().getMonth()+1))
		   	  	{
			   	  	    dom_top=`
		   	  	       <div class="datetime_main">
		               <div class="datetime_top"><span class="prev"   ><</span>`+t.year+"年"+t.month+`月<span class="next" >></span></div>
		               <div class="datetime_week"><table><tr><td>一</td><td>二</td><td>三</td><td>四</td><td>五</td><td>六</td><td>日</td></tr></table></table></div>
		               <div class="datetime_content">
		           	  <ul>
		   	  	    `
			   	  	
		   	  	}
		   	  	else{
			   	  		dom_top=`
		   	  	       <div class="datetime_main" style="display:none;">
		               <div class="datetime_top"><span class="prev"><</span>`+t.year+"年"+t.month+`月<span class="next" >></span></div>
		               <div class="datetime_week"><table><tr><td>一</td><td>二</td><td>三</td><td>四</td><td>五</td><td>六</td><td>日</td></tr></table></table></div>
		               <div class="datetime_content">
		           	  <ul>
		   	  	    `
			   	  	
		   	  	}
	   	  	var days;
	   	  	if(t.is30(t.month)){days=30;}else{if(t.month==2){t.isRyear()==true ? days=29:days=28;}else{days=31;}}
	   	  	var str="";
	   	  	for(let d=1;d<days+1;d++)
	   	  	{
	   	  		var dat=t.formatDate(t.year,t.month,d,"-")
	   	  		if(d==1)
	   	  		{
	   	  			var week=new Date(dat).getDay();
	   	  			week==0 ? week=7 : week=week;
	   	  			for(var w=1;w<week;w++){str=str+"<li></li>"}
	   	  		}
	   	  		var zhuru=10,total=10;
	   	  	    try{
	   	  	    	
	   	  		   total=j[t.year][t.month][d].total;
	   	  		   
	   	  		   
	   	  		   str=str+"<li data-year='"+t.year+"'  data-month='"+t.month+"' data-day='"+t.month[d]+"' ref='dat' class='li succ' data-id='"+dat+"' data-total="+total+"><div class='datetime_date'>"+d+"</div><div class='datetime_total'>￥"+total+"</div></li>"
	
	   	  	    }catch(e){
	   	  	    	zhuru=false;
	   	  	    	total=0;
	   	  	    	str=str+"<li data-year='"+t.year+"'  data-month='"+t.month+"' data-day='"+t.month[d]+"' ref='dat'  class='li err' data-id='"+dat+"' data-total="+total+"><div class='datetime_date'>"+d+"</div><div class='datetime_total'></div></li>"
	
	   	  	    }
	   	  	}
	   	  	str=dom_top+str+dom_bottom;
	   	  	
	   	  	this.picker=this.picker+str;
	   	    t.month+=1;
	   	  }
	   },
	   formatDate:(y,m,d,s)=>{
	   	   m > 9 ? m = m +'' :m = '0'+ m;
	   	   d > 9 ? d = d +'' :d = '0'+ d;
	   	   return y + s + m + s + d;
	   },
	
	}
	t.insert()
  },
  
  mounted(){
  	
  }
};

</script>


