<style>
	.xingcheng_nav{ position: relative; border-bottom: 1px solid #f6f6f6;}
	.xingcheng_nav ul{
		margin:0; padding: 0;
	   background: #fff; padding: 1rem 0rem;display: flex; display: -webkit-flex;
	   justify-content: center;align-items: center;
	   
	}
	.xingcheng_nav ul li{ font-size:1.4rem;flex:auto; text-align: center; border-left:1px solid #ddd}
	.xingcheng_nav ul li:first-child{ border-left:none}
	.xingcheng_nav ul .navactive{ color: #FF414F;}
	.xingcheng_nav label{
		transition:0.2s;
		 width:13.3333%; position: absolute; left:10%; bottom:0px; height: 2px; background: url(../assets/bj.jpg);}

    .xingcheng_content .content{ padding-bottom: 3rem;}
    .xingcheng_content .activecontent{ display: block !important;}
    
    .waiting{  text-align: center; padding: 1rem 0rem;}
    .waiting img{ width:7rem ;height: auto; display: inline-block; margin: 0 auto;}
    .wrapper ul { padding: 0rem 1rem; overflow-y:scroll ; }
    .wrapper ul li{  border:1px solid #eee; height: auto; background: #fff; margin-top: 0.5rem; border-radius:10px; overflow: hidden;}

    .none{ height: 100%; position: relative;}
    .none img{ position: absolute; left: 0px; right: 0px; width:150px; margin: auto; top:50%; transform:translateY(-50%); -webkit-transform:translateY(-50%);  }

    .wrapper ul li a{ display: block; width:100%; height: 100%;
    display:flex; display: -webkit-flex;
     flex-flow: row nowrap;
    }
    .wrapper ul li a .list1{ flex:none; width: 12rem; overflow: hidden;}
    .wrapper ul li a .list1 img{ width:100%; height: 100%;}
    .wrapper ul li a .list2{ flex:auto; display:flex; display: -webkit-flex;
    justify-content: space-around; align-content: center; flex-flow:column;
     margin-left: 0.5rem;
    }
    .wrapper ul li a .list2 span{ padding-top:0.5rem;}
    .wrapper ul li a .list2 .list2_name{ font-size: 1.2rem; display: flex; display: -webkit-flex;
      flex-flow:row  nowrap;   align-items: center; line-height: 2rem;
    }
     .wrapper ul li a .list2 .list2_name label{ flex:auto}
      .wrapper ul li a .list2 .list2_name i{ flex:none; font-style: normal; color: #FF414F; font-size: 1.0rem; margin-right:0.5rem;}
    .wrapper ul li a .list2 .list2_total{ padding-bottom: 0.5rem; font-size: 1.45rem; 
       display: flex; display: -webkit-flex;
      flex-flow:row  nowrap;   align-items: center;
    }
    .wrapper ul li a .list2 .list2_total label{ flex:auto; color: #FF414F; }
    .wrapper ul li a .list2 .list2_total button{ font-size:1.0rem; flex:none;margin-right: 0.3rem; border: none; background: url(../assets/bj.jpg); padding: 0.3rem 0.5rem; color: #fff; border-radius:5px;}
    .wrapper ul li a .list2 .list2_total .payOrder{ background: none; color: #FF414F; border: 1px solid #FF414F;box-sizing: border-box}
    .wrapper ul li a .list2 .list2_date,.wrapper ul li a .list2 .list2_data{ font-size: 1.0rem; color: dimgray;}
   
</style>
<template>
	<div class="xingcheng">
		<div class="xingcheng_nav">
			<ul @click="navClick">
				<li data-id="1" class="navactive">待入住</li>
			    <li data-id="2">入住中</li>
			    <li data-id="3">已完成</li>
			</ul>
			<label ref="moveLable"></label>
		</div>
		
			<div class="wrapper xingcheng_content" ref="wrapper" style="; height:calc(100vh - 7rem); overflow: hidden; ">
				  <ul @click="orderClick"  ref="ref1" data-id="1" class="content activecontent" style="min-height: calc(100vh - 6rem);">
					<li v-for="item in splist">
						
					</li>
					<li >
					    <a href="#" class="">
							<div class="list1"><img src="../../static/img/index/index1.png"/></div>
							<div class="list2">
								<span class="list2_name"><label>怕看看崎逡的浪费城堡</label><i>待付款</i></span>
								<span class="list2_date">2015/06/12——2015/06/15</span>
								<span class="list2_data">共6晚&nbsp;&nbsp;&nbsp; 居1人</span>
							    <span class="list2_total"><label>￥890.00</label><button  data-id="001" class="payOrder">待付款</button><button data-id="001" class="orderCancel">取消订单</button></span>
							</div>
							
						</a>
					</li>
					
					<li >
					    <a href="#" class="">
							<div class="list1"><img src="../../static/img/index/index1.png"/></div>
							<div class="list2">
								<span class="list2_name"><label>怕看看崎逡的浪费城堡</label><i>待付款</i></span>
								<span class="list2_date">2015/06/12——2015/06/15</span>
								<span class="list2_data">共6晚&nbsp;&nbsp;&nbsp; 居1人</span>
							    <span class="list2_total"><label>￥890.00</label><button data-id="001">待付款</button><button>待付款</button></span>
							</div>
							
						</a>
					</li>
					
					<li >
					    <a href="#" class="">
							<div class="list1"><img src="../../static/img/index/index1.png"/></div>
							<div class="list2">
								<span class="list2_name"><label>怕看看崎逡的浪费城堡</label><i>待付款</i></span>
								<span class="list2_date">2015/06/12——2015/06/15</span>
								<span class="list2_data">共6晚&nbsp;&nbsp;&nbsp; 居1人</span>
							    <span class="list2_total"><label>￥890.00</label><button>待付款</button><button>待付款</button></span>
							</div>
							
						</a>
					</li>
				
				  </ul>
			  <!-- 这里可以放一些其它的 DOM，但不会影响滚动 -->
			</div>
			<myconfirm :show="confirm.show" :mess="confirm.mess" @confirmSure="confirmSure"  @confirmCancel="confirmCancel"></myconfirm>
			<none :show="noneShow"></none>
	</div>
</template>

<script>
import BScroll from 'better-scroll'
import myconfirm from   '@/components/confirm.vue'
import none from   '@/components/none.vue'
import $ from "jquery"
export default {
	name:"xingcheng",
	 components: { myconfirm,none },
	data(){
		return{
			splist:[],
			splist1:[{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"},{"xm":"1"}],
			splist2:[{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2},{"xm":2}],
			splist3:[{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3},{"xm":3}],
		    noneShow:false,
		    confirm:{
		    	ddbh:"001",
		    	show:false,
		    	mess:"aaaaa",
		    }
		}
	},
	methods:{
		orderClick(event){
			event.stopPropagation()
			var o=event.target;
			var classname=o.getAttribute("class");
			if(classname=="orderCancel")
			{
				this.confirm.ddbh=o.getAttribute("data-id");
				this.confirm.mess="确定要取消订单吗?";
				this.confirm.show=true;
				
			}
		},
		confirmSure(){
			alert("删除编号为"+this.confirm.ddbh+"的订单")
			this.confirm.show=false;
		},
		confirmCancel(){
			this.confirm.show=false;
		},
		navClick(event){
			$(".activecontent").animate({scrollTop:0}, 500);
			$(".waiting").remove();
			var o=event.target;
			if(o.nodeName!="LI"){return;}
			var did=o.getAttribute("data-id");
			var objs=document.querySelectorAll(".xingcheng_nav ul li");
			for(let i=0;i<objs.length;i++)
			{
				objs[i].setAttribute("class","")
			}
			o.setAttribute("class","navactive")
			if(did=="1"){
				this.$refs.moveLable.style.left="10%"
				this.splist=this.splist1;
			}
			if(did=="2"){
				this.$refs.moveLable.style.left="43.3%"
				this.splist=this.splist2;
			}
			if(did=="3"){
				this.$refs.moveLable.style.left="76.6%"
				this.splist=this.splist3
			}
			$(".wait").remove();
		},
		
		_createWait(){
			$(".waiting").remove();
			var div=document.createElement("div")
			div.setAttribute("class","waiting");
			var img=document.createElement("img")
			img.src="/static/img/wait2.gif";
			div.appendChild(img);
			return div;
		},
		_createEnd(){
			var div=document.createElement("div");
			div.innerHTML="没了";
			return div;
		},
		_createNone(){
			var div=document.createElement("div");
			div.setAttribute("class","none");
			var img=document.createElement("img")
			img.src="../../static/img/none.png";
			div.appendChild(img);
			return div;
		}
	},
	watch:{
		splist:function(){
			 this.$nextTick(function(){
			 	this.scroll.scrollTo(0, 0, 300)
			 })
		}
	},
	mounted(){
		//$(".xingcheng_nav ul li:first-child").click()
		//this.split=this.splist1;
		this.$nextTick(()=>{
		   this.scroll = new BScroll(this.$refs.wrapper, {})
	            this.scroll.on('scrollEnd', (pos) => {
	            	//alert(55)
			    if(pos.y==this.scroll.maxScrollY)
				{
					var o=this._createWait();
					this.$refs.ref1.appendChild(o)
					this.scroll.refresh()
				}
				    console.log(this.scroll.maxScrollY)
				     console.log(pos)
				})
		})
	},
   
};
</script>




