<template>
    <div id="map_container">
    </div>
</template>


</script>
<script>
/*import { TMap } from '../TMap'*/
export default {
    data() {
        return {
        	markerArray:[],
        	map:null,
        }
    },
    mounted() {
    	
         this.createMap();
    	   
    },
    methods: {
	 createMap()
	 {
	 	 var that=this;
	     var latlng=new qq.maps.LatLng(this.qbl.lat,this.qbl.lng);
	 	 that.map = new qq.maps.Map(document.getElementById("map_container"), {
              center: latlng,
              zoom:12
        });
        var marker = new qq.maps.Marker({
	                        map: that.map,
	                        position: latlng
	    });
	    that.markerArray.push(marker);
	    var listenr=qq.maps.event.addListener(
					that.map,
					'click',
					function(e){
						that.addPoint(e.latLng.getLat(),e.latLng.getLng())
		            }
		)
	  },
	    addPoint(x,y)
		{
			
			this.clearOverlays()
			var that=this;
			var latlng=new qq.maps.LatLng(x, y);
			/*查询城高昌*/
			var  citylocation = new qq.maps.CityService();
			var x=citylocation.searchCityByLatLng(latlng)
				var markerObj=new qq.maps.Marker
				({
					position:latlng,
					map:that.map,
					
			    })
				markerObj.setAnimation(qq.maps.MarkerAnimation.DOWN);
				this.markerArray.push(markerObj);
		},
		clearOverlays() 
		{
			var that=this;
		    if (this.markerArray) 
		    {
		        for (var i=0;i<that.markerArray.length;i++) 
		        {
		            that.markerArray[i].setMap(null);
		        }
		    }
		},
    },
    created: function () {
    	
    }
}
</script>
<style>
#map_container {
    min-width:600px;
    height:calc(100vh - 5rem);
}
</style>


