import Vue from 'vue'
import Router from 'vue-router'

import HelloWorld from '@/components/HelloWorld'
import Myrouter from '@/components/Myrouter'
import BetterScroll from "@/components/better-scroll"
import Swiper from "@/components/swiper"
import City from "@/components/city"
import Picker from "@/components/picker"
import Picker2 from "@/components/picker2"
import Picker3 from "@/components/picker3"
import Index from '@/components/Index'

import Dingfang from '@/components/dingfang'
import Map from '@/components/map'
import Xingcheng from '@/components/xingcheng'
import My from '@/components/my'
import CitySort from '@/components/citysort'
import HotCity from '@/components/hotcity'
import IndexSearch from '@/components/indexsearch'

Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
	  meta:{
                title:'首页',
                index:1
            }
	   
    },
	{
		path:"/a/:id/:post",
		name:"Myrouter",
		component:Myrouter,
		meta:{
                title:'myrouter页面',
                index:2
            }
	},
	{
		path:"/scroll",
		name:"better-scroll",
		component:BetterScroll,
		meta:{
			title:"下拉加载页面",
			index:3,
		}
	},
	{
		path:"/swiper",
		name:"swiper",
		component:Swiper,
		meta:{
			title:"图片轮播",
			index:4,
		}
	},
	{
		path:"/city",
		name:"city",
		component:City,
		meta:{
			title:"city",
			index:5,
		}
	},
	{
		path:"/picker",
		name:"picker",
		component:Picker,
		meta:{
			title:"picker",
			index:6,
		}
	},
	{
		path:"/picker2",
		name:"picker2",
		component:Picker2,
		meta:{
			title:"picker2",
			index:7,
		}
	},
	{
		path:"/picker3",
		name:"picker3",
		component:Picker3,
		meta:{
			title:"picker3",
			index:8,
		}
	},
	
	{
		path:"/dingfang",
		name:"dingfang",
		component:Dingfang,
		meta:{
			title:"订房",
			index:10,
		}
		
	},
	{
		path:"/map",
		name:"map",
		component:Map,
		meta:{
			title:"地图",
			index:11,
		}
	},
	{
		path:"/xingcheng",
		name:"xingcheng",
		component:Xingcheng,
		meta:{
			title:"行程",
			index:12,
		}
	},
	{
		path:"/my",
		name:"my",
		component:My,
		meta:{
			title:"个人中心",
			index:13,
		}
	},
	{
		path:"/citysort",
		name:"citysort",
		component:CitySort,
		meta:{
			title:"城市选择",
			index:14,
		}
	},
	{
		path:"/hotcity/:id/:title",
		name:"hotcity",
		component:HotCity,
		meta:{
			title:"热门城市",
			index:15,
		}
	},
	{
		path:"/indexsearch",
		name:"indexsearch",
		component:IndexSearch,
		meta:{
			title:"搜索",
			index:16,
		}
	},
	
	
  ],
  
	
})



