<style>
	.citysort{ height: 100%; width:100%; background: #fff;}
	.citysort_main{ position:relative; padding-bottom: 7rem;}

	.citysort_main div p{ padding: 0rem 1rem;; background: #f6f6f6; height: 3rem; line-height: 3rem; font-size: 1.2rem;}
    .citysort_main_city ul{ margin: 0px; padding: 0px; overflow:hidden;}
    .citysort_main_city ul li{ padding: 1rem 1rem; height: 1.5rem ;line-height: 1.5rem; font-size: 1.2rem; border-bottom: 1px solid #f6f6f6;}
    .citysort_main_char{ height: 16px; line-height: 16px;: hidden; height: auto; position: fixed; right:1rem; top:50%;transform: translateY(-50%);-webkit-transform: translateY(-50%);}

    .citysort_main_char  a{height: 16px; line-height: 16px; color: #FF414F;}
</style>
